import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: '홈',
    component: HomeView
  },
  {
    path: '/rooms',
    name: '객실안내',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/rooms/RoomsListView.vue')
    }
  },
	{
    path: '/rooms/detail/:contentId',
    name: '객실안내 상세',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/rooms/RoomsDetailView.vue')
    },
		props: true
  },
  {
    path: '/reserve',
    name: '예약목록',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/reserve/ReserveListView.vue')
    }
  },
	{
    path: '/reserve/create',
    name: '예약하기',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/reserve/ReserveDetailView.vue')
    },
		props: true
  },
	{
    path: '/reserve/read',
    name: '예약상세',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/reserve/ReserveDetailView.vue')
    },
		props: true
  },
	{
    path: '/reserve/update',
    name: '예약수정',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/reserve/ReserveDetailView.vue')
    },
		props: true
  },
	{
    path: '/contact',
    name: '펜션소개',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/contact/ContactView.vue')
    }
  },
	
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
